import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import meter1 from "../assets/img/react.svg";
import meter2 from "../assets/img/jquery.svg";
import meter3 from "../assets/img/typescript.svg";
import meter4 from "../assets/img/c-sharp.svg";
import meter5 from "../assets/img/dot-net.svg";
import meter6 from "../assets/img/oracle.svg";

export default function Skill() {
    const responsive = {
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1
        }
      };

  return (
    <div className="skill" id="skills">
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <div className="skill-bx wow zoomIn">
                        <h2>技能</h2>
                        <Carousel responsive={responsive} infinite={true} className="skill-slider">
                            <div className="item">
                                <img src={meter1} alt="Image" />
                                <h5>React</h5>
                            </div>
                            <div className="item">
                                <img src={meter2} alt="Image" />
                                <h5>jQuery</h5>
                            </div>
                            <div className="item">
                                <img src={meter3} alt="Image" />
                                <h5>TypeScript</h5>
                            </div>
                            <div className="item">
                                <img src={meter4} alt="Image" />
                                <h5>C#</h5>
                            </div>
                            <div className="item">
                                <img src={meter5} alt="Image" />
                                <h5>ASP.NET</h5>
                            </div>
                            <div className="item">
                                <img src={meter6} alt="Image" />
                                <h5>Oracle</h5>
                            </div>
                        </Carousel>
                    </div>
                </div>
            </div>
        </div>
    </div>
  )
}
